:root {
  --indent: 20px;
  --header-height: 100px;
}

html,
body {
  height: 100%;
}

body {
  min-width: 320px;
  margin: 0;
  line-height: 1.3;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: #3e1f05;
  text-size-adjust: 100%;
}

h1,
h2,
h3,
h4 {
  font-weight: 600;
  font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a:not(.button) {
  transition: opacity 0.25s ease-in-out;
}

a:not(.button):hover {
  opacity: 0.8;
}

a:not(.button):active {
  opacity: 0.6;
}

section {
  position: relative;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: var(--indent);
}

.logo {
  display: block;
}

.logo img {
  display: block;
  width: 40px;
  height: 40px;
}

.button {
  display: inline-block;
  padding: 12px 24px;
  font-weight: 600;
  font-size: 16px;
  line-height: 125%;
  font-family: inherit;
  text-align: center;
  color: #fdfdfd;
  text-decoration: none;
  letter-spacing: 0.192px;
  background-color: #1A3E3E;
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
  border: 0;
  border-radius: 4px;
  box-shadow: 0 3px 0 #6a4e26, 0 0 90px #ffffff;
  transition: box-shadow 0.25s ease-in-out, opacity 0.25s ease-in-out;
  appearance: none;
}

.button:not(:disabled):hover {
  box-shadow: 0 0 0 #6a4e26, 0 0 90px #ffffff;
  cursor: pointer;
}

.button:not(:disabled):active {
  opacity: 0.7;
}

.button::before {
  content: "";
  display: inline-block;
  height: 1em;
  vertical-align: middle;
}

.icon-link {
  display: inline-flex;
  align-items: center;
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.icon-link::before {
  content: "";
  display: block;
  width: 25px;
  height: 25px;
  margin-right: 10px;
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
  mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
}

.icon-link--telegramm::before {
  mask-image: url(../../img/telegram.svg);
}

.icon-link--youtube::before {
  mask-image: url(../../img/youtube.svg);
}

.title {
  margin: 0 0 1.5em;
  font-size: 1.5em;
}

.animation-rotate {
  --transition-duration: 200s;
  width: 60px;
  height: 60px;
  mask-image: url(../../img/logo-circle.svg);
  mask-repeat: no-repeat;
  mask-size: contain;
  animation: rotate var(--transition-duration) linear infinite;
  z-index: 1;
  pointer-events: none;
}

.animation-rotate::before {
  content: "";
  display: block;
  top: -200%;
  width: 100%;
  height: 100%;
  right: 0;
  animation: rotate var(--transition-duration) linear infinite reverse;
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: var(--header-height);
  color: #fff;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__link {
  margin-left: auto;
}

.main {
  overflow: hidden;
}

.hero {
  position: relative;
  padding-top: var(--header-height);
  padding-bottom: 0;
  color: #ffffff;
  background-color: #3e1f05;
  background-image: url(../../img/hero-bg.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

.hero__container {
  position: relative;
  padding-bottom: calc(var(--indent) * 2);
}

.hero__container::after {
  content: "";
  display: block;
  height: 130%;
  background-image: url(../../img/hero-img.png);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: contain;
}

.hero__title {
  letter-spacing: 2px;
  text-wrap-style: balance;
}

.gallery__container {
  padding-top: 5em;
  position: relative;
}

.gallery__animation-rotate {
  position: absolute;
  top: -150px;
  right: -150px;
  width: 300px;
  height: 300px;
}

.gallery__list {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, minmax(225px, 1fr));
  margin: 0;
  padding: 0;
  list-style-type: none;
  justify-items: center;
  gap: var(--indent);
}

.gallery__list li {
  border: 1px solid #d4b383;
  padding: 5px;
}

.gallery__list img {
  display: block;
  object-position: center;
}

.popup {
  position: fixed;
  z-index: 1;
  display: flex;
  align-items: center;
  backdrop-filter: blur(20px);
  inset: 0;
}

.popup::before {
  content: "";
  position: absolute;
  z-index: -1;
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
  opacity: 0.3;
  inset: 0;
}

.popup__container {
  width: fit-content;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 6px;
  backdrop-filter: blur(20px);
  padding: calc(var(--indent) * 2);
}

.popup__close {
  appearance: none;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: absolute;
  top: -24px;
  right: -24px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  backdrop-filter: blur(20px);
}

.popup__close:hover {
  opacity: 0.8;
  cursor: pointer;
}

.popup__close:active {
  opacity: 0.6;
}

.popup__close::before {
  content: "";
  position: absolute;
  inset: 0;
  mask-image: url(../../img/close.svg);
  mask-size: contain;
  mask-position: center;
  mask-repeat: no-repeat;
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
}

.form {
  display: grid;
  gap: var(--indent);
}

.form__title {
  margin-bottom: 0;
}

.input {
  box-sizing: border-box;
  height: 44px;
  padding: 12px 15px;
  font-size: inherit;
  line-height: 1;
  font-family: inherit;
  color: inherit;
  background-color: #fff;
  border: 1px solid #bbb;
  border-width: 0;
  border-radius: 6px;
}

.footer {
  padding: var(--indent);
  text-align: center;
}

@media (min-width: 445px) {
  .hero__container {
    display: grid;
    align-items: center;
    grid-template-columns: 50% 1fr;
  }
}

@media (min-width: 768px) {
  .button {
    padding: 18px 30px;
    font-size: 18px;
    border-radius: 6px;
  }

  .title {
    font-size: 1.7em;
  }
}

@media (min-width: 890px) {
  .title {
    font-size: 2em;
  }
}

@media (min-width: 990px) {
  .gallery__list img {
    filter: grayscale(75%);
    transition: filter 0.25s ease-in-out;
  }

  .gallery__list img:hover {
    filter: grayscale(0);
  }
}

@media (min-width: 1000px) {
  .title {
    font-size: 2.7em;
  }

  .header__logo img {
    width: 60px;
    height: 60px;
  }

  .hero__container {
    min-height: 470px;
  }

  .hero__container::after {
    height: 100%;
  }
}

@media (max-width: 1000px) {
  .hero__container::after {
    background-position: center bottom;
  }
}

@media (max-width: 768px) {
  .gallery__animation-rotate {
    display: none;
  }
}

@media (max-width: 445px) {
  .hero__container::before {
    display: none;
  }
}